<template>
  <div class="main-container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item>诗词</el-breadcrumb-item>
      <el-breadcrumb-item v-if="pageOption.totalCount > 0">{{ pageOption.totalCount }} 首</el-breadcrumb-item>
    </el-breadcrumb>
    <el-divider />
    <el-row v-if="tableData.length > 0" :gutter="10">
      <el-col v-for="item in tableData" :key="item.id" :xs="24" :sm="12" :md="8" :lg="6" :xl="4">
        <el-card class="box-card">
          <div slot="header" class="clearfix" style="height: 22px;">
            <span class="title">
              {{ item.title }}
            </span>
          </div>
          <el-divider content-position="left" class="mtop12">{{ item.dynastyName }} ● <el-tooltip effect="light">
            <div slot="content">{{ item.authorIntro }}</div>
            <span>{{ item.authorName }}</span></el-tooltip>
          </el-divider>
          <div class="text item" v-html="item.content" />
        </el-card>
      </el-col>
    </el-row>
    <el-row v-else>
      <el-col v-if="!loading" :xs="24" :sm="12" :md="8" :lg="6" :xl="4">
        未搜索到相关内容
      </el-col>
    </el-row>
    <el-row style="text-align: center;">
      <el-button v-if="loading" type="text" class="load-icon" disabled><i class="el-icon-loading" /></el-button>
      <el-button v-else-if="hasPage" type="text" @click="nextPage()">加载更多</el-button>
    </el-row>
  </div>
</template>

<script>
import { getList } from '@/api/article/peotry'
import { debug } from '@/utils'
import { isEmpty } from '@/utils/validate'

export default {
  name: 'Poetry',
  data() {
    return {
      apiUrl: '/poetry',
      formData: {
        page: 1,
        perPage: 12
      },
      hasPage: true,
      loading: true,
      tableData: [],
      pageOption: {
        totalCount: 0,
        perPage: 10,
        pageCount: 0,
        currentPage: 1
      }
    }
  },
  created() {
    this.search(1)
  },
  methods: {
    nextPage() {
      this.loading = true
      this.formData.page++
      this.search(this.formData.page)
    },
    headerSearch(value) {
      this.loading = true
      this.hasPage = true
      this.tableData = []
      this.formData.title = value
      this.apiUrl = isEmpty(value) ? '/poetry' : '/poetry/search'
      this.search(1)
    },
    search(page) {
      this.loading = true
      this.formData.page = page
      getList(this.formData, this.apiUrl).then(response => {
        if (response.status === 'success') {
          const items = response.data.items
          this.pageOption.totalCount = response.data.page === undefined ? 0 : (response.data.page.totalCount || 0)
          this.pageOption.perPage = response.data.page === undefined ? 0 : (response.data.page.perPage || 10)
          this.pageOption.pageCount = response.data.page === undefined ? 0 : (response.data.page.pageCount || 0)

          if (this.formData.page >= this.pageOption.pageCount) {
            this.hasPage = false
          }

          if (items.length > 0) {
            for (var i in items) {
              this.tableData.push({
                id: items[i].id,
                title: items[i].title,
                statusName: items[i].statusName,
                authorName: items[i].authorName,
                authorIntro: items[i].authorIntro,
                dynastyName: items[i].dynastyName,
                content: items[i].content,
                createDate: items[i].createDate
              })
            }
          }
        }
        this.loading = false
      }).catch(error => {
        debug(error)
        this.loading = false
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.main-container {
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }

    .load-icon {
      font-size: 28px;
      color: #389cd6;
    }
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
    height: 160px;
    overflow: auto;
    white-space: nowrap;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    min-width: 280px;
    margin-top: 10px;

    .title {
      font-weight: bold;
      width: 100%;
      float: left;
      white-space: nowrap;
      overflow: auto;
    }
  }
}
</style>
